* {
    margin: 0;
    padding: 0;
}

#star_for_o{
  color: #ff0000;
  font-size: small;
}

body{
    background-color: rgb(76, 76, 76);
    color: #fff;
    font-size: large;
}
#window{
  visibility: hidden;
  position: absolute;
    background-color: #ffffff;
    color: black;
    width: 150px;
    top: 5%;
  font-size: 12px;
    height: 100px;
    padding: 30px;
    overflow: auto;
    border: 1px solid #ddd;
    z-index: 1;
  }

button{
    width: 80px;
    height: 30px;
    background-color: rgb(21, 120, 120);
    border-style: none;
    color: #fff;
}
.dropbtn {
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    width: 100px;
    text-align: center;
    border: none;
    cursor: pointer;
  }
  
  .dropbtn:hover, .dropbtn:focus {
    background-color: #3e8e41;
  }
  
  #myInput {
    box-sizing: border-box;
    background-position: 14px 12px;
    background-repeat: no-repeat;
    font-size: 16px;
    padding: 14px 20px 12px 45px;
    border: none;
    border-bottom: 1px solid #ddd;
  }
  
  #myInput:focus {outline: 3px solid #ddd;}
  
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    position: absolute;
    background-color: #f6f6f6;
    min-width: 230px;
    overflow: auto;
    border: 1px solid #ddd;
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown a:hover {background-color: #ddd;}
  
  .show {display: block;}